import uniSection from '@/components/uni-section/uni-section.vue'
import uniRequest from 'uni-request'
import myIssue from '@/components/myIssue.vue'
export default {
	data(){
	     return {
	         // 这里循环出未点亮的星分和点亮的星分
			 tabCurrentIndex: 0,
			 goodsItem:{},
			 desc:'',
			 infoReceive:{
				 score:5,
				 is_anonymous:false,
				 textareaValue:'',
				 headPicValue:''
			 },
	     }
	 },
	 components:{uniSection,myIssue},
	 onLoad(options){
		 console.log(JSON.parse(options.item))
		 this.goodsItem = JSON.parse(options.item)
		 console.log(this.infoReceive.is_anonymous)
	 },
	 onShow(){
	 	if(uni.getStorageSync('userInfo').token){
	 		if(uni.getStorageSync('userInfo').code){
	 			this.infoReceive.headPicValue = 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570534827025&di=11e89dd29f4faec93dd0de848fb379cc&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01c8415b10ec7aa801212d57334fdc.png%401280w_1l_2o_100sh.png'
			}else{
	 			this.infoReceive.headPicValue = 'http://img.zcool.cn/community/01786557e4a6fa0000018c1bf080ca.png'
			}
	 	} 
	 },
	 methods: {
		   async submit(e){
			   console.log(e)
			   const response = await uniRequest({
			   	url:'user/goods/comment/',
			   	method:'post',
				data:{
					sku: this.goodsItem.sku_id,
					order: this.goodsItem.orderId,
					comment:e.textareaValue,
					is_anonymous:e.is_anonymous,
					score:e.score
				},
			   	headers:{
			   		Authorization:'JWT '+uni.getStorageSync('userInfo').token
			   	},
			   }).then(res=>{
				   console.log(res)
				   if(res.status === 201){
					   this.$api.msg('评论成功')
					   this.getAssess()
				   }else{
					   this.$api.msg(res.data.order[0])
				   }
			   }).catch(error=>{
				   console.log(error)
			   })
		   },
			getAssess(e){
				var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面
				//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				prevPage.setData({
				     isDoRefresh:true
				})
				 setTimeout(function() {
					  uni.navigateBack();
				 }, 600);
			},
			
			checkboxChange(check){
				this.infoReceive.is_anonymous = !check
				console.log(this.infoReceive.is_anonymous)
				if(this.infoReceive.is_anonymous === true){
					this.infoReceive.headPicValue = '../../../static/missing-face.png'
				}else{
					if(uni.getStorageSync('userInfo').token){
						if(uni.getStorageSync('userInfo').code){
							this.infoReceive.headPicValue = 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570534827025&di=11e89dd29f4faec93dd0de848fb379cc&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01c8415b10ec7aa801212d57334fdc.png%401280w_1l_2o_100sh.png'
						}else{
							this.infoReceive.headPicValue = 'http://img.zcool.cn/community/01786557e4a6fa0000018c1bf080ca.png'
						}
					} 
				}
			}
	 },
}
